import React, { Component } from "react";

class Navbar extends Component {
  render() {
    return (
      <div>
        {this.props.lists.map((list) => {
          return list.showme === false ? (
            <div key={list.id}>
              <span>ID: {list.id}</span>
              <br />
              <span>Name:{list.Name}</span>
              <button onClick={() => this.props.disableproduct(list.id)}>
                Disable
              </button>
              <button onClick={() => this.props.Enableproduct(list.id)}>
                Enable
              </button>
            </div>
          ) : null;
        })}
        {/* <div>
          {this.props.lists.map((list) => {
            return list.showme === true ? (
              <div key={list.id}>
                <span>ID: {list.id}</span>
                <br />
                <span>Name:{list.Name}</span>
              </div>
            ) : null;
          })}
        </div> */}
      </div>
    );
  }
}

export default Navbar;
