import React, { Component } from "react";
import Body from "./Component/Body";
import Navbar from "./Component/Navbar";
import Enable from "./Component/enableproduct";
import Disable from "./Component/disableproduct";
class App extends Component {
  state = {
    id: "",
    Name: "",
    showme: false,
    items: [],
    list: [],
    Enable: [],
    Disable: [],
  };
  style = {
    fontSize: 10,
    fontWeight: "bold",
  };
  handledelete = (key) => {
    const items = this.state.items.filter((item) => item.id !== key);
    this.setState({ items });
    console.log({ items });
  };

  handlesubmit = () => {
    const item = this.state.items;
    const id = this.state.id;
    const _name = this.state.Name;
    const Showme = false;

    item.push({ id: id, Name: _name, showme: Showme });
    this.setState({
      id: id,
      Name: _name,
      showme: Showme,
    });
    this.setState({ id: "", Name: "" });
    console.log({ item });
  };

  disableproduct = (counter) => {
    const listse = [...this.state.list];

    listse.forEach((lists) => {
      if (lists.id === counter) {
        lists.showme = false;
      }
    });
    const data = [...listse];
    this.setState({ list: data });
    console.log({ data });
  };
  Enableproduct = (counter) => {
    const listse = [...this.state.list];

    listse.forEach((lists) => {
      if (lists.id === counter) {
        lists.showme = true;
      }
    });
    const data = [...listse];
    this.setState({ list: data });
    console.log({ data });
  };

  allproduct = () => {
    const lists = this.state.items;

    this.setState({ list: lists });
    console.log({ lists });
  };

  enable = () => {
    const enable = this.state.list.filter((c) => c.showme === true);
    this.setState({ Enable: enable });
  };
  disable = () => {
    const disable = this.state.list.filter((c) => c.showme === false);
    this.setState({ Disable: disable });
  };

  render() {
    return (
      <div className="App">
        enable= {this.state.list.filter((c) => c.showme === true).length}
        disable={this.state.list.filter((c) => c.showme === false).length}
        <nav className="navbar navbar-light bg-light">
          <button className="btn btn-sm btn-primary" onClick={this.allproduct}>
            All product
          </button>

          <button onClick={this.enable} className="btn btn-sm btn-primary">
            Enable product{" "}
          </button>
          <button onClick={this.disable} className="btn btn-sm btn-primary">
            Disable product{" "}
          </button>
        </nav>
        <div>
          <label style={this.style}>ID:</label>
          <input
            style={this.style}
            type="text"
            placeholder="Enter product ID"
            value={this.state.id}
            name="id"
            onChange={(e) => this.setState({ id: e.target.value })}
          ></input>
          <label style={this.style}>Name:</label>
          <input
            style={this.style}
            type="text"
            placeholder="Enter product Name"
            value={this.state.Name}
            name="Name"
            onChange={(e) => this.setState({ Name: e.target.value })}
          ></input>
          <button
            onClick={this.handlesubmit}
            style={this.style}
            className="btn btn-sm btn-primary"
          >
            Add product
          </button>
        </div>
        <div>
          <Body items={this.state.items} onDelete={this.handledelete} />
        </div>
        <div>
          <Navbar
            lists={this.state.list}
            Enableproduct={this.Enableproduct}
            disableproduct={this.disableproduct}
          />
        </div>
        <div>
          <Enable lists={this.state.Enable} />
        </div>
        <div>
          <Disable lists={this.state.Disable} />
        </div>
      </div>
    );
  }
}

export default App;
