import React, { Component } from "react";
class Size extends Component {
  state = {
    list: [],
    inputvalue: { id: "", Size: "" },
  };

  handleinput = (e) => {
    this.setState({
      inputvalue: {
        Size: e.target.value,
        id: Date.now(),
      },
    });
  };

  addlist = (e) => {
    const list = this.state.inputvalue;
    if (list.Size !== "") {
      const lists = [...this.state.list, list];
      this.setState({
        list: lists,
        inputvalue: { id: "", Size: "" },
      });

      console.log({ list });
    }
  };
  deletelist = (key) => {
    const list = this.state.list.filter((lists) => lists.id !== key);
    this.setState({ list });
  };

  render() {
    return (
      <div className="App">
        <div>
          <label>Enter Product Size</label>
          <input
            placeholder="Enter Size"
            onChange={this.handleinput}
            value={this.state.inputvalue.Size}
          />
          <button onClick={this.addlist} className="btn btn-info btn-sm m-2">
            Add Size
          </button>
          <br />
        </div>

        <div>
          {this.state.list.map((lists) => (
            <span key={lists.id}>
              {lists.Size}

              <button
                className="btn btn-danger btn-sm"
                onClick={() => this.deletelist(lists.id)}
              >
                cut
              </button>
            </span>
          ))}
        </div>
      </div>
    );
  }
}

export default Size;
