import React, { Component } from "react";
class Disable extends Component {
  render() {
    return (
      <div>
        {this.props.lists.map((list) => (
          <div key={list.id}>
            <span>{list.id}</span>
            <span>{list.Name}</span>
          </div>
        ))}
      </div>
    );
  }
}

export default Disable;
